<template>
  <div class="data-table-wrapper">
    <table class="page-datatable" v-if="dataList.length != 0">
      <thead>
        <tr>
          <th>
            <span>Klantnaam</span>
            <base-icon iconRef="chevron-down" position="right"/>
          </th>
          <th>
            <span>Contactpersoon</span>
            <base-icon iconRef="chevron-down" position="right"/>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="dataEntry in dataList" :key="dataEntry.id" @click="$emit('click', dataEntry.id)">
          <td>
            <span>{{dataEntry.data().clientName}}</span>
          </td>
          <td>
            <span>{{`${dataEntry.data().contact.firstName} ${dataEntry.data().contact.lastName}`}}</span>
          </td>
        </tr>
      </tbody>
    </table>
    <p class="page-datatable-disclaimer" v-if="dataList.length == 0">Er zijn geen klanten gevonden.</p>
  </div>
</template>

<script>
export default {
  name: "clients-data-list",
  props: {
    dataList: {
      type: Array
    }
  }
};
</script>

<style>
</style>



